.blog-info {
	@include rmin(700) {
		@include flex; } }

.article-meta {
	width: 21%;
	margin-top: 10px;
	margin-right: 10px;

	@include r(699) {
		@include flex;
		justify-content: space-around;
		width: 100%; }

	h5 {
		color: $dark-orange;
		text-transform: uppercase;
		font-family: Bebas;
		@include fs(12);
		font-weight: 400;
		margin-top: 10px;
		margin-bottom: 10px; }

	p {
		font-family: HelveticaNeue;
		@include fs(12);
		color: #ddd; } }



.blog-article {
	width: 100%;
	margin-top: 17px;

	@include rmin(700) {
		width: 79%; }

	p {
		font-family: HelveticaNeue;
		font-weight: 400;
		@include fs(12);
		line-height: 1.5;
		margin-top: 17px;

		@include rmin(320) {
			text-align: justify; }
		@include rmin(700) {
			text-align: left; } } }
