@include font(Bebas, Bebas);

a {
    color: #000;
    text-decoration: none; }

.container {

	width: 960px;
	margin-right: auto;
	margin-left: auto;
	padding-left: 10px;
	padding-right: 10px;

	@include rmin(200) {
		width: 200px; }
	@include rmin(320) {
		width: 80%; }
	@include rmin(480) {
		width: 90%; }
	@include rmin(940) {
		width: 80%; }
	@include rmin(1100) {
		width: 65%; }
	@include rmin(1600) {
		width: 51%; } }

.container--flex {
	@include flex; }

.main-container {
	position: relative;
	@include rmin(320) {
		margin-right: 0; }
	@include rmin(480) {
		width: 70%;
		margin-right: 25px; }
	@include r(480) {
		width: 100%; } }

.blog-h2 {
	color: $dark-orange;
	text-transform: uppercase;
	font-family: Bebas;
	@include fs(18);
	font-weight: 400; }

.pages-number {
	@include flex;
	margin-bottom: 80px;

	span {
		display: block;
		height: 25px;
		width: 25px;
		font-family: Bebas;
		text-align: center;
		margin-right: 4px;
		color: #FFF;
		background-color: $dark-orange; } }
