.quote-article {
	background-color: #F4F4F4;
	padding: 20px 0;
	&>svg {
		fill: $dark-orange;
		font-size: 1.5em;
		margin-right: 15px;
		margin-left: 35px;
		float: left; }
	&>p {
		overflow: hidden;
		@include fs(13);
		font-family: HelveticaNeue;
		font-style: italic;
		font-weight: 600;
		line-height: 1.4;
		padding-right: 15px;
		text-align: justify; } }
