@media only screen and (max-width: 479px) { .menuToggle { display: block; position: relative; z-index: 1; -webkit-user-select: none; user-select: none; width: 40px; height: 32px; top: -30px; left: -20px; }
  .menuToggle input { display: block; width: 40px; height: 32px; position: absolute; top: -7px; left: -5px; cursor: pointer; opacity: 0; z-index: 2; -webkit-touch-callout: none; }
  .menuToggle span { display: block; width: 33px; height: 4px; margin-bottom: 5px; position: relative; background: #e8603c; border-radius: 3px; z-index: 1; transform-origin: 4px 0px; transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }
  .menuToggle span:first-child { transform-origin: 0% 0%; }
  .menuToggle span:nth-last-child(2) { transform-origin: 0% 100%; }
  .menuToggle input:checked ~ span:nth-last-child(3) { opacity: 0; opacity-transform: rotate(0deg) scale(0.2, 0.2); }
  .menuToggle input:checked ~ span:nth-last-child(2) { transform: rotate(-45deg) translate(0, -1px); }
  .menuToggle input:checked ~ span { transform: rotate(45deg) translate(0, -1px); }
  .menu { position: absolute; width: 360px; margin: -70px 0 0 -50px; padding-top: 60px; padding-bottom: 40px; background: #f3af9d; list-style-type: none; -webkit-font-smoothing: antialiased; transform-origin: 0% 0%; transform: translate(-100%, 0); transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }
  .menu li { padding: 10px 110px; font-size: 22px; float: none; }
  .menuToggle input:checked ~ ul { transform: none; } }

