.icon-frame {
	background-color: $dark-orange;
	position: absolute;

	@include rmin(320) {
		display: none; }

	@include rmin(700) {
		display: block;
		width: 30px;
		height: 30px;
		left: -35px; }

	@include rmin(800) {
		width: 40px;
		height: 40px;
		left: -50px; }

	@include rmin(940) {
		width: 70px;
		height: 70px;
		left: -80px; } }

.icon-frame__blog {
	position: relative;
	top: 20%;
	left: 20%; }

.icon-frame--big {
	&>svg {
		fill: #fff; }

	@include rmin(480) {
		&>svg {
			width: 1.2em;
			height: 1.1em; } }

	@include rmin(800) {
		&>svg {
			width: 1.4em;
			height: 1.3em; } }

	@include rmin(940) {
		&>svg {
			width: 2.4em;
			height: 2.3em; } } }

.icon-link--big {
	@include rmin(940) {
		&>svg {
			width: 3.4em;
			height: 3.3em; } } }
