.aside-bar {
	margin-top: -35px;
	font-family: HelveticaNeue;
	@include rmin(320) {
		@include flex;
		top: 11%;
		flex-wrap: wrap; }

	@include rmin(480) {
		width: 25.5%;
		display: block; }

	@include rmin(800) {
		margin-left: 25px; }

	@include r(480) {
		position: absolute; } }

.aside-fixed {
	position: fixed;
	top: 5%;

	@include rmin(480) {
		right: 2%;
		width: 24%; }

	@include rmin(1100) {
		right: 2%;
		width: 24%; }

	@include rmin(1600) {
		right: 24%;
		width: 12%; } }

.aside__line {
	@include flex;
	margin-top: 35px;
	margin-bottom: 20px;

	h5 {
		text-transform: uppercase;
		color: $dark-orange;
		font-family: Bebas;
		@include fs(14);
		white-space: nowrap;
		margin-right: 15px;
		cursor: pointer; } }

.about-us-small {
	p {
		font-family: HelveticaNeue;
		@include fs(12); } }


.categories-small {
	@include fs(12); }

.js-toggle-info {

	@include rmin(320) {
		display: none;
		position: absolute;
		background-color: rgba(255,222,222, 0.8);
		top: 70%; }

	@include rmin(480) {
		display: block;
		position: relative;
		background-color: transparent; } }

.gallery-small {
	@include flex;
	flex-wrap: wrap; }


.gallery-small__item {
	position: relative;
	height: 47px;
	width: 47px;
	overflow: hidden;
	margin: 6px;
	border: 1px solid $dark-orange;
	background-color: #ef957d;
	cursor: pointer; }


/* увеличенная картинка */
.js-magnify {
	display: none;
	position: fixed;
	max-width: 900px;
	height: auto;
	z-index: 9999; }


.js-magnify {
	img {
		width: 100%; } }


/* затемняющий фон */
.overlay {
	display: none;
	background: #000;
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	opacity: 0.5;
	z-index: 9990; }

/* кнопка закрытия */
.close-popup {
	width: 30px;
	height: 30px;
	background: #FFFFFF;
	border: 1px solid #AFAFAF;
	border-radius: 15px;
	cursor: pointer;
	position: absolute;
	top: 15px;
	right: 15px; }


.close-popup i {
	width: 30px;
	height: 30px;
	background: url(https://codernote.ru/files/cross.png) no-repeat center center;
	background-size: 16px 16px;
	display: block; }


@keyframes rota {
	25% { transform: rotate(360deg); }; }


.close-popup {
	&:hover {
		animation: rota 4s infinite normal;
		-webkit-animation-iteration-count: 1;
		animation-iteration-count: 1; } }


