.header-line {
	@include flex;
	margin-bottom: 23px;

	@include rmin(320) {
		height: 30px; }

	@include rmin(560) {
		height: 40px; }

	@include rmin(700) {
		height: 60px; } }

.title-line {
	background-color: $dark-orange;

	@include rmin(700) {
		width: 100%;
		height: 60px; }

	@include rmin(560) {
		height: 40px; }

	@include rmin(320) {
		height: 30px; }


	&--h2 {
		color: #FFF;
		font-family: Bebas;
		font-weight: 400;

		@include rmin(320) {
			@include fs(20);
			margin-left: 15px; }

		@include rmin(560) {
			margin-left: 15px;
			padding-top: 5px; }

		@include rmin(700) {
			@include fs(24);
			margin-left: 18px;
			padding-top: 13px; } } }


.aside-line {
	background-color: $light-orange;

	@include rmin(320) {
		width: 9.5%; }

	@include rmin(480) {
		width: 4.5%; }

	@include rmin(940) {
		width: 10%; }

	@include rmin(1100) {
		width: 17.5%; }

	@include rmin(1600) {
		width: 24.5%; } }




